 
 /**
  * 
  * @param {*} num
  */
 export const happenTimeFun=(num)=> { //
 	let date = new Date(num*1000);
 	//10*1000131000
 	let y = date.getFullYear();
 	let MM = date.getMonth() + 1;
 	MM = MM < 10 ? ('0' + MM) : MM; //0
 	let d = date.getDate();
 	d = d < 10 ? ('0' + d) : d; //0
 	let h = date.getHours();
 	h = h < 10 ? ('0' + h) : h; //0
 	let m = date.getMinutes();
 	m = m < 10 ? ('0' + m) : m; //0
 	let s = date.getSeconds();
 	s = s < 10 ? ('0' + s) : s; //0
 	return y + '-' + MM + '-' + d;
 	// return y + '-' + MM + '-' + d + ' ' + h + ':' + m+ ':' + s;
 };
 
 
 /**
  * 
  * @param {*} s
  */
 export const isMobile = (s) => {
 	return /^1[0-9]{10}$/.test(s)
 }
 
 /**
  * 
  * @param {*} s
  */
 export const sliceWord = (s) => {
 	return s.slice(0,3)
 }